import React from "react";
import { connect } from "react-redux";
import Style from "./style.module.css";
import * as actions from "../../redux/action/burgerActions";
import BurgerContext from "../../context/BurgerContext";

const BuildControl = (props) => {
  return (
    <div className={Style.BuildControl}>
      <div className={Style.Label}>{props.orts}</div>
      <button
        disabled={props.disabled[props.type]}
        onClick={() => props.ortsHasah(props.type)}
        className={Style.Less}
      >
        Хасах
      </button>
      <button
        onClick={() => props.ortsNemeh(props.type)}
        className={Style.More}
      >
        Нэмэх
      </button>
    </div>
  );
};

const mapDispatchToProps = (dispatch) => {
  return {
    ortsNemeh: (ortsNer) => {
      dispatch(actions.addIngirient(ortsNer));
    },
    ortsHasah: (ortsNer) => {
      dispatch(actions.removeIngirient(ortsNer));
    },
  };
};

export default connect(null, mapDispatchToProps)(BuildControl);
